.card {
  box-sizing: border-box;
  border: 1px solid #d6d4d4;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-between;
  width: 200px;
  height: auto;
  padding: 32px 16px 16px;
  display: flex;
}

.card__product-image-mac {
  width: 100%;
  height: auto;
  display: block;
}

.card__item-name {
  margin: 34px 0 0;
  font-size: 12px;
  font-weight: 500;
}

.card__item-code {
  color: #616070;
  margin: 4px 0;
  font-size: 10px;
}

.review {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.review__comments {
  font-size: 10px;
  line-height: 14px;
}

.price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.price__item {
  color: #616070;
  font-size: 12px;
}

.price__amount {
  font-size: 16px;
  font-weight: 700;
}

.buy-button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background: #00acdc;
  border-radius: 5px;
  align-content: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
}

.buy-button:hover {
  color: #00acdc;
  box-sizing: border-box;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.review__star {
  display: flex;
}

.review__star-stars {
  background-image: url("star.0aa15587.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.review__star .review__star-stars:nth-child(-n+4) {
  background-image: url("star-active.1a24ce14.svg");
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 400;
  line-height: 18px;
}
