.review__star {
  display: flex;

  &-stars {
    background-image: url(../images/star.svg);
    background-repeat: no-repeat;
    background-position: center;
    width: 16px;
    height: 16px;
    margin-right: 4px;
  }

  & .review__star-stars:nth-child(-n + 4) {
    background-image: url(../images/star-active.svg);
  }
}
